('/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n.likertInteraction{\n    width:100%;\n    padding:10px 20px 16px !important;\n    margin-bottom:20px;\n    background-image: url(\"data:image/png;base64,\");\n    background-repeat: repeat;\n}\n\n.likertInteraction ul.likert{\n    list-style-type: none;\n    margin: 0 5px;\n    padding: 0;\n    display:inline-block;\n    position: relative;\n    top:7px;\n}\n\n.likertInteraction ul.likert li{\n    float: left;\n    text-align: left;\n    list-style-type: none;\n}\n\n.likertInteraction ul.likert li input{margin: 0 3px; cursor: pointer;}\n\n.likertInteraction .prompt {position:relative;}\n\n.likertInteraction .likert-label {min-width:90px; color: #444;}\n.likertInteraction .likert-label-min {text-align: right;}\n.likertInteraction .likert-label-max {text-align: left;}\n.likertInteraction .likert-label img {position:relative; width:16px;}');
